
.wrap_content{
   padding-bottom:1rem;
}
.wrap_content  a{
    font-size:12px;
    color: #999999;
}
.wrap_content ul li{
    padding: 0 1rem;
}
.wrap_content h3{
    padding: 1rem ;
    font-weight: bold;
}
.display_none{
    display: none;
}
.main_title{
    overflow: hidden;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    text-align: center;
   height: 3.6rem;
   line-height: 3.6rem;
   width:100%;
}
.main_title li{
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    -webkit-transition:all 0.3s ease;
    transition: all 0.3s ease;
}
.main_title li.active{
    color: #03a9f4;
    font-size: 16px;
    font-weight: bold;
}
.swiper-pagination-bullet-custom{
     width: 100% !important;
    height: 100% !important;
    margin:0 !important;
    text-align: center;
    line-height: 50px;
    color: #999999;
    border-radius: 0 !important;
    background: #fff !important;
    opacity: 1 !important;
}
.swiper-pagination-bullet-custom.swiper-pagination-bullet-active {
    color: #03a9f4;
    font-size: 16px;
    font-weight: bold;
}




